.table-admin{
	position:relative;
	flex: 0 1 20%;
	display: flex;
	flex-direction: column;
	align-items: center;

	&:hover{
		opacity:0.5;
		cursor:pointer;
	}
	
	.ui.label{
		margin:0;
		font-size:20px;
		position:absolute;
		top:30px;
	}
	
	svg{
		heigth:200px;
		width:200px;
	
		&.busy{
			fill:#bb7900;
		}
		&.pending{
			fill:#0094e3;
		}
		&.pending-payment{
			fill:#008000;
		}
	}
		

}
